/* RESET */

html {}
html body {line-height: 1;}
html body p {line-height: 20px;}

* {

	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
	
}

a, abbr, acronym, address, applet, area, article, aside, audio, b, base, basefont, bdi, bdo, big, blockquote, body, br, button, canvas, caption, center, cite, code, col, colgroup, command, datalist, dd, del, details, dfn, dialog, dir, div, dl, dt, em, embed, fieldset, figcaption, figure, font, footer, form, frame, frameset, h1, h2, h3, h4, h5, h6, head, header, hgroup, hr, html, i, iframe, img, input, ins, kbd, keygen, label, legend, li, link, main, map, mark, menu, meta, meter, nav, noframes, noscript, object, ol, optgroup, option, output, p, param, pre, progress, q, rp, rt, ruby, s, samp, script, section, select, small, source, span, strike, strong, style, sub, summary, sup, table, tbody, td, textarea, tfoot, th, thead, time, title, tr, track, tt, u, ul, var, video, wbr {margin: 0; padding: 0; border: none;}

a {

	-webkit-transition: all 300ms cubic-bezier(0.250, 0.100, 0.250, 1.000); 
	   -moz-transition: all 300ms cubic-bezier(0.250, 0.100, 0.250, 1.000); 
	     -o-transition: all 300ms cubic-bezier(0.250, 0.100, 0.250, 1.000); 
	        transition: all 300ms cubic-bezier(0.250, 0.100, 0.250, 1.000); /* ease (default) */

	-webkit-transition-timing-function: cubic-bezier(0.250, 0.100, 0.250, 1.000); 
	   -moz-transition-timing-function: cubic-bezier(0.250, 0.100, 0.250, 1.000); 
	     -o-transition-timing-function: cubic-bezier(0.250, 0.100, 0.250, 1.000); 
	        transition-timing-function: cubic-bezier(0.250, 0.100, 0.250, 1.000); /* ease (default) */

}

b, strong {font-weight: bold;}
i, em {font-style: italic;}
img {max-width: 100%; height: auto;}
form * {outline: none;}

span, small, em {
	font-weight: inherit;
}

footer a {text-decoration: none;}

::selection {color:#fff; background:rgba(0,0,0,0.5);}
::-moz-selection {color:#fff; background:rgba(0,0,0,0.5);}

/* ttulos */

h1, h2, h3, h4, h5, h6 {font-weight: normal;}

h1 {font-size: 48px;}
h2 {font-size: 36px;}
h3 {font-size: 30px;}
h4 {font-size: 24px;}
h5 {font-size: 18px;}
h6 {font-size: 14px;}

/* listas */

nav ul {list-style-type: none;}
nav ul li a {text-decoration: none;}
.txt ul {padding: 0 0 0 30px; list-style: disc;}
.txt ul li {}
.txt ol {padding: 0 0 0 30px; list-style: decimal;}
.txt ol li {}
footer ul {list-style: none;}

/* formulrios */

form {}
form * {}
form fieldset {}
form fieldset label {}

form select {}
form select optgroup {}
form select option {}

form input[type="text"] {}
form input[type="radio"] {}
form input[type="checkbox"] {}
form input[type="email"] {}
form input[type="text"], form input[type="email"], form textarea {padding: 10px 15px; overflow: hidden;}

form input[type="text"].erro, form input[type="email"].erro, form textarea.erro {

	border-color: #ba1523!important;

}

form input[type="submit"] {cursor: pointer;}
form input[type="submit"]:hover {

/*	-webkit-opacity:0.8;
	-moz-opacity:0.8;
	-o-opacity:0.8;
	-ms-opacity:0.8;
	opacity:0.8;*/
}
form input[type="submit"]:disabled {
	background: #666!important;
}

form textarea {
    resize: none;
    box-sizing: border-box;
    padding: 10px 15px;
}
button {}
button, input[type="submit"] {
	cursor: pointer;
}

