.choices {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 0 20px 0;
  label {
    margin: 10px;

    .bloco_choice {
      background: #fff;
      width: 145px;
      height: 145px;
      display: block;
      position: relative;
      border-radius: 40px;
      overflow: hidden;
    }

    figure {
      // width: calc(100% - 30px);
      text-align: center;
      margin: auto;
      padding: 10px 10px 0 10px;
      height: 100%;
      img {
        width: auto;
        display: block;
        margin: auto;
        max-height: 65%;
      }
      figcaption {
        font-weight: 600;
        text-transform: uppercase;
        padding: 10px 0 0;
        font-size: 12.5px;
      }
    }

    .check {
      position: absolute;
      top: 15px;
      right: 15px;
      width: 30px;
      height: 30px;
      // border: solid 2px #2ECC71;
      border: solid 2px #999;
      border-radius: 100px;
      background: #fff;
      i {
        width: 15px;
        margin: auto;
        display: none;
      }
    }

    input {
      position: absolute;
      opacity: 0;

      &:checked {

        &+.bloco_choice {
          -webkit-transform: scale(1.05);
          box-shadow: 0 20px 30px rgba(0,0,0,0.3);
          .check {
            border-color: #2ECC71;
            i {
              display: block;
            }
          }
        }
      }
    }
  }
}

